<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item class="search">
        <el-input
          v-model="form.id"
          placeholder="广告id"
          @keyup.enter.native="onSubmit"
          @change="onSubmit"
          @blur="onSubmit"
        ></el-input>
        <el-input
          v-model="form.ownerId"
          placeholder="用户ID"
          @keyup.enter.native="onSubmit"
          @blur="onSubmit"
          @change="onSubmit"
        ></el-input>
        <el-input
          v-model="form.ADtheme"
          placeholder="主题"
          @blur="onSubmit"
          @keyup.enter.native="onSubmit"
          @change="onSubmit"
        ></el-input>
        <el-button
          icon="el-icon-search"
          type="primary"
          size="medium"
          autocomplete="off"
          @click="onSubmit"
          @keyup.enter.native="onSubmit"
          >搜索</el-button
        >
        <el-button
          class="el-icon-delete"
          type="danger"
          size="medium"
          autocomplete="off"
          @click="clear"
          @keyup.enter.native="clear"
          >清空</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Search",

  data() {
    return {
      form: {
        id: "",
        ownerId: "",
        ADtheme: "",
      },
    };
  },

  mounted() {},

  methods: {
    onSubmit() {
      // 如果form为空
      if (
        this.form.id === "" &&
        this.form.ownerId === "" &&
        this.form.ADtheme === ""
      ) {
        this.$axios.get("/admin/getAll?currentPage=1").then((res) => {
          this.$emit("search", res.data.data.records);
        });
        return;
      }
      this.$axios.post("/search", this.form).then((res) => {
        this.$emit("search", res.data.data);
      });
    },
    clear() {
      this.form = {};
      this.$axios.get("/admin/getAll?currentPage=1").then((res) => {
        this.$emit("search", res.data.data.records);
      });
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  display: flex;
  justify-content: space-between;
}
.el-input {
  width: 200px;
  margin: 10px 50px auto 0;
}
</style>