<template>
  <div class="edit">
    <div style="width: 100%">
      <el-form
        ref="userForm"
        :model="userForm"
        label-width="90px"
        style="
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          align-items: center;
          wrap: wrap;
        "
      >
        <el-form-item label="ID">
          <el-input v-model="userForm.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="投放者姓名">
          <el-input v-model="userForm.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="userForm.phone" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="userForm.email" disabled></el-input>
        </el-form-item>
      </el-form>
      <el-table :data="tableData" border style="width: 100%; dispaly: block">
        <el-table-column prop="img" label="广告图片" width="180">
          <template slot-scope="scope">
            <img :src="scope.row.img" width="100%" alt="" />
          </template>
        </el-table-column>
        <el-table-column prop="date" label="截止时间" width="180">
        </el-table-column>
        <el-table-column prop="content" label="内容摘要"> </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Edit",
  props: {
    form: {
      type: Object,
      default() {
        return {
          id: "",
          name: "",
          address: "",
        };
      },
    },
  },

  data() {
    return {
      userForm: this.form,
      tableData: [],
    };
  },

  mounted() {},

  watch: {
    form: {
      handler(val) {
        this.userForm = val;
        this.getAD();
      },
      deep: true,
    },
  },

  created() {
    this.getAD();
  },

  methods: {
    // onSubmit() {
    //   var date1 = this.$moment(this.userForm.date1).format("YYYY-MM-DD");
    //   var date2 = this.$moment(this.userForm.date2).format("HH:mm:ss");
    //   this.userForm.date = date1 + " " + date2;
    // },
    getAD() {
      this.$axios
        .get("/admin/getAD?id=" + this.userForm.id)
        .then((res) => {
          this.userForm = res.data.data;
          this.userForm = res.data.data;
          this.tableData.push(res.data.data.AD);
          this.tableData[0].date = this.$moment(this.tableData[0].date).format(
            "YYYY-MM-DD HH:mm:ss"
          );
        })
        .catch((err) => {
          //   错误提示
          this.$message({
            type: "error",
            message: "获取失败",
          });
        });
    },
  },
};
</script>

<style lang="less" scoped>
.edit {
  display: flex;
  padding: 20px;
}
</style>