<template>
  <div>
    <el-container>
      <el-header>
        <Search @search="searchRes"></Search>
      </el-header>
      <el-main>
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="id" label="ID" width="80"> </el-table-column>
          <el-table-column prop="ownerId" label="ownerId" width="100">
          </el-table-column>
          <el-table-column
            prop="adtheme"
            label="广告主题"
            show-overflow-tooltip
            width="150"
          >
          </el-table-column>
          <el-table-column label="状态" width="100">
            <template slot-scope="state">
              <el-tooltip :content="'状态:' + state.row.state" placement="top">
                <el-switch
                  v-model="state.row.state"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  active-value="正常"
                  inactive-value="冻结"
                  @change="changeState(state.row)"
                >
                </el-switch>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column
            prop="content"
            label="内容摘要"
            show-overflow-tooltip
            width="400"
          >
          </el-table-column>
          <el-table-column label="操作">
            <template slot="header">
              <el-button
                type="danger"
                class="el-icon-warning"
                @click="delBatch"
                style="margin-left: 22px"
              >
                批量删除</el-button
              >
            </template>
            <template slot-scope="scope">
              <el-button
                label="ttb"
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"
                ><i class="el-icon-star-off"></i> 详情</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"
                ><i class="el-icon-delete"></i> 删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer style="height: 20px, line-height=20px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="6"
          layout="prev, pager, next, jumper"
          :total="this.total"
        >
        </el-pagination>
      </el-footer>
    </el-container>
    <el-drawer
      :direction="direction"
      :visible.sync="drawer"
      :with-header="false"
      size="50%"
    >
      <span><Edit :form="this.form"></Edit></span>
    </el-drawer>
  </div>
</template>

<script>
import Search from "./Search.vue";
import Edit from "./Edit.vue";
export default {
  name: "Allad",

  components: { Search, Edit },

  data() {
    return {
      tableData: [],
      multipleSelection: [],
      currentPage: 1,
      total: 20,
      drawer: false,
      direction: "ttb",
      form: {},
    };
  },
  mounted() {},

  created() {
    this.getAll();
  },

  methods: {
    getAll() {
      this.$axios
        .get("/admin/getAll?currentPage=" + this.currentPage)
        .then((res) => {
          this.tableData = res.data.data.records;
          this.currentPage = res.data.data.current;
          this.total = res.data.data.total;
        });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return "warning-row";
      } else if (rowIndex === 3) {
        return "success-row";
      }
      return "";
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      // 往数组内添加
      this.multipleSelection = val.map((item) => {
        return item.id;
      });
    },
    handleEdit(index, row) {
      this.form = row;
      this.drawer = true;
    },
    handleDelete(index, row) {
      this.$axios.delte("/admin/delete?id=" + row.id).then((res) => {
        this.getAll();
      });
    },
    searchRes(res) {
      this.tableData = res;
    },
    changeState(row) {
      var form = {
        id: row.id,
        state: row.state,
      };
      this.$axios.post("/admin/changeState", form).then((res) => {
        this.$message.success(res.data.data);
      });
    },
    delBatch() {
      // 确认操作
      this.$confirm("此操作将删除所有选中单位, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // 确认删除
          this.$axios
            .post("/admin/delBatch", {
              ids: this.multipleSelection,
            })
            .then((res) => {
              if (res.data.code == 200) {
                this.$message({
                  type: "success",
                  message: "删除成功!",
                });
                this.multipleSelection = [];
                this.$refs.multipleTable.clearSelection();
                this.getAll();
              } else {
                this.$message({
                  type: "error",
                  message: "删除失败!",
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getAll();
    },
  },
};
</script>

<style lang="less" scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
/deep/.el-table th {
  height: 60px;
  line-height: 60px;
}
/deep/.el-pagination {
  height: 16px;
  line-height: 16px;
}
</style>